<template>
  <Layout>
    <div class="password-login">
      <div class="password-form">
        <h1>密码登录</h1>
        <div class="input-wrapper">
          <input type="tel"
                 placeholder="请输入手机号"
                 maxlength="11"
                 v-model="phoneNumber">
          <input type="password"
                 placeholder="请输入密码"
                 minlength="6"
                 v-model="password">
          <button :disabled="check" @click="submit">立即登录</button>
          <div>
            <router-link to="/login">
              短信验证码登录
            </router-link>
            <router-link to="/resetPassword">
              忘记密码？
            </router-link>
          </div>
        </div>
        <div class="agree-protocol"
             @click="agree">
          <Icon v-if="agreeProtocol" name="success"/>
          <span v-else class="circle"></span>
          <p>已阅读并同意<span>《用户服务协议》《隐私权政策》</span></p>
        </div>
      </div>
    </div>
  </Layout>
</template>

<script>
export default {
  name: 'PasswordToLogin',
  data() {
    return {
      phoneNumber: '',
      password: '',
      agreeProtocol: false
    }
  },
  computed: {
    check() {
      if (!this.agreeProtocol ||
          this.phoneNumber.length !== 11 ||
          this.password.length < 6) {
        return true
      } else {
        return !(/^1[3456789]\d{9}$/.test(this.phoneNumber))
      }
    }
  },
  methods: {
    agree() {
      this.agreeProtocol = !this.agreeProtocol
    },
    submit() {
      console.log(1)
      console.log(this.phoneNumber)
    }
  }
}
</script>

